@import url(https://fonts.googleapis.com/css2?family=Heebo:wght@300;400&display=swap);
body {
  margin: 0;
 font-family: 'Heebo', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


/* width */
::-webkit-scrollbar-track
{
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
    border-radius: 1px;
    background-color: #131111;
}

::-webkit-scrollbar
{
    width: 5px;
    background-color: #000000;
}

::-webkit-scrollbar-thumb
{
    border-radius: 10px;
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);
    background: linear-gradient(to left, #333, #333 50%, #eee 75%, #333 75%);
}
.svg{
         display: grid;
         grid-template-areas: "svg-card"
                                "welcome";
    
        text-align: center;
        justify-content: center;
        align-content: center;
}
.svg-card{
        grid-area: svg-card;
}
.welcome{
        grid-area: welcome;

}
@-webkit-keyframes welcome{
  0%   {  
        font-size: 10px;
        visibility: visible;
        }
}
@keyframes welcome{
  0%   {  
        font-size: 10px;
        visibility: visible;
        }
}
.welcome-p{
        color: #808080;
         -webkit-animation:welcome 5s;
                 animation:welcome 5s; 
         cursor: pointer;
}

.welcome-p:hover{
                color: #ff0000; 

}
@media only screen and (min-width: 600px) {
                .svg-card{
                    width: 800px; 
                    max-height: 550px;
                }
                .welcome-p{
                    font-size: 50px;
                   }
        }
@media only screen and (max-width: 600px) {
                .svg-card{
                   width: 450px; 
                    max-height: 350px; 
                }
                .welcome-p{
                    font-size: 30px;
                 
            }
        }
@media only screen and (max-width: 400px) {
                .svg-card{
                    max-width: 250px;
                    max-height: 200px;
                }
                .welcome-p{
                    font-size: 30px;
                 
            }
        }

*, *:after, *:before{
    overflow-x: hidden;
}

.APP{
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none; 
  background-color: black;

  display: flex;
  flex-direction: column;
  
}
.pll{
    min-height: 100vh; 
}
.pll-1{
    display:grid;
    background-image: linear-gradient(180deg, white 40%, black 60%);
    cursor: default; 
}
.pll-2, .pll-3, .pll-4{
  display:grid;
  align-content: center;
  padding: 2% 2% 2% 2%; 
}

.pll-3{
 background-image: linear-gradient(90deg, #202226 50%, black 50%);
 display: grid;
 align-items: center;
}
/* EXTRA ABOUT */
.EXABOUT{
  display: grid; 
  border-radius:20px; 
  padding: 2% 2% 3% 2%;
  overflow: hidden;
}
.EXABOUT-content{
  grid-area: EXABOUT-content; 
   display:flex;
  flex-direction: column;
}
#EXABOUT-info-ava{
  pointer-events: none;
  -webkit-filter: grayscale();
          filter: grayscale();
  border-bottom: 2px #000000 solid;
}

.EXABOUT-info-contact{
   list-style-type: none;
}

.EXABOUT-info-contact li a{
    text-decoration: none;
  }


.EXABOUT-ability, .EXABOUT-experience , .EXABOUT-education{
  border-radius: 20px;
  border: 2px #000000 solid; 
  text-align: center;
  padding: 0 2% 2% 0;
}
        

.EXABOUT-identity{
      grid-area: EXABOUT-identity; 
}
.EXABOUT-ability{
      grid-area: EXABOUT-ability; 
}
.EXABOUT-experience{
  grid-area: EXABOUT-experience;

}
.EXABOUT-education{
  grid-area: EXABOUT-education;
}
.EXABOUT-button{
  grid-area: EXABOUT-button;
  justify-self:center;
}
.EXABOUT-button-symbol:hover{
  border-bottom: 2px black solid;
}


  .EXABOUT-experience:hover, .EXABOUT-ability:hover, .EXABOUT-education:hover{
      box-shadow: 2px 2px 20px gray;
      border: none; 
}
 .EXABOUT-title{
   font-weight: bold;
 }
