/* Skill Page */

.SKILL{
 background-image: linear-gradient(90deg, #202226 50%, black 50%);
 display: grid;
 grid-template-areas: "SKILL-half-left SKILL-half-right";
  color: #ffffff;
  justify-items: center;
  align-items: center;
}

.SKILL-half-left{
  grid-area: SKILL-half-left;
 font-size: 70px;
 -webkit-text-stroke-width: 0.5px;
  -webkit-text-stroke-color: #ffffff;

}

